.board {
    width: 1000px;
    height:1000px;
    border: 5px solid black;
    background-color: none;
    box-sizing: border-box;
    touch-action: auto;
}

.sketch {
    width: 1000px;
    height: 1000px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5px;
}

.black {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: black;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.white {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid black;
    background-color: white;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.colors {
    display: flex;
    gap: 20px;
    bottom: 0;
    margin: 20px;
}

body {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 200px;
}

h1 {
    font-size: 70px;
    font-family: sans-serif;
    margin-bottom: 0;
    padding-bottom: 0;
}

.heading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: flex-start;
    margin-bottom: 40px;
}

p {
    font-family: sans-serif;
}